h2 {
  font-size: 3rem;
}

h1 {
  font-size: 4rem;
  color: #1f618d;
}

.Contact-us {
  padding: 5rem 2rem;
  background-color: #badcf3;
}

.Contact-us .header h1 {
  margin-bottom: 2rem;
}

.Contact-us .contact-content {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  gap: 3rem;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
}

.Contact-us .contact-content .contact-location {
  width: 100%;
  -webkit-box-flex: 40rem;
      -ms-flex: 40rem;
          flex: 40rem;
}

.Contact-us .contact-content .contact-location .location-map iframe {
  width: 100%;
  height: 45rem;
}

.Contact-us .contact-content .contactForm {
  width: 100%;
  -webkit-box-flex: 40rem;
      -ms-flex: 40rem;
          flex: 40rem;
  height: 45rem;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  background-color: white;
  padding-top: 2rem;
}

.Contact-us .contact-content .contactForm form {
  padding: 0rem 3rem;
}

.Contact-us .contact-content .contactForm input,
.Contact-us .contact-content .contactForm textarea {
  border: none;
  border-bottom: 0.1rem solid rgba(0, 0, 0, 0.3);
}
